@btn-dark-bg: #403744;
@btn-dark-border: #5C5365;
@btn-white-bg: #EDF6FF;
@btn-white-border: #F4FAFF;

@opacity-hover: 0.7;
@white-hover: rgba(240,247,255,0.8);
@icon-white-hover: rgba(255,255,255,0.6);
@icon-dark-hover: rgba(53,53,68,0.6);

@mobile-width: 420px;
// @tablet-width: 755px;
@tablet-width: 48em;
@desktop-width: 1690px;
@max-desktop-width: 1921px;

@retina-dpi: 144dpi;
@retina-dppx: 1.5dppx;

// @s: 0.5s;

@a: auto;

@solid: 1px solid red;

.o {
  outline: 1px solid red;
}

.o(@color) {
  outline: 1px solid @color;
}

.r {
  border: 1px solid red;
}

.b(@color) {
  border: 1px solid @color;
}

.text-stroke {
  text-shadow: #000 1px 1px 0,
               #000 -1px -1px 0,
               #000 -1px 1px 0,
               #000 1px -1px 0;
}

.text-stroke(@color, @offset) {
  text-shadow: @color @offset @offset 0,
               @color -@offset -@offset 0,
               @color -@offset @offset 0,
               @color @offset -@offset 0;
}

.reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

.mp-reset(@margin; @padding) {
  margin: @margin;
  padding: @padding;
}

.img-cover {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.img-contain {
  width: 100%;
  height: 100%;
  object-fit: contain;
}

.margin-block(@value) {
  margin-top: @value;
  margin-bottom: @value;
}

.margin-block(@value1, @value2) {
  margin-top: @value1;
  margin-bottom: @value2;
}

.margin-inline(@value) {
  margin-left: @value;
  margin-right: @value;
}

.margin-inline(@value1, @value2) {
  margin-left: @value1;
  margin-right: @value2;
}

.padding-block(@value) {
  padding-top: @value;
  padding-bottom: @value;
}

.padding-block(@value1, @value2) {
  padding-top: @value1;
  padding-bottom: @value2;
}

.padding-inline(@value) {
  padding-left: @value;
  padding-right: @value;
}

.padding-inline(@value1, @value2) {
  padding-left: @value1;
  padding-right: @value2;
}
